.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 0;
    position: sticky;
    top: 0;
    height: 5.5rem;
    width: 100vw;
    z-index: 10;
    background-color: #f5f5f5;
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.1);
}

.title {
    flex-grow: 0;
    font-size: 3rem;
    font-weight: 600;
    background-image: linear-gradient(to left, #000000, #ffffff);
    color: transparent;
    background-clip: text;
    cursor: default;
}

.sections {
    width: 50vw;
    display: flex;
    justify-content: space-between;
}

.sections.mobile {
    width: 95vw;
}

.section {
    cursor: pointer;
    transition: 0.5s;
}

.active {
    color: #000000;
    text-shadow: 0 0 2px #444444;
}

.section a {
    color: inherit;
    text-decoration: none;
}

.section:hover {
    transition: 0.5s;
    color: #000000;
    text-shadow: 0 0 2px #444444;
}
